/* eslint-disable eqeqeq */
import React, { useEffect, useState } from "react";
import { Button, Container, makeStyles, Typography } from "@material-ui/core";
import { Link, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  getByIdService,
  setIsDetailLoading,
} from "../../../redux/reducers/pay24";
import Balance from "../../../components/Balance";
import CabinetTop from "../../../components/cabinetTop";
import SkeletonPayments from "./Skeleton";
import { Box } from "@mui/system";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import { ClickAwayListener, Grid, IconButton, Tooltip } from "@mui/material";

const Services = ({ t, main }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { servicesId } = useParams();

  const [country, setCountry] = useState("");
  const [open, setOpen] = useState(false);

  const handleTooltipClose = () => {
    setOpen(false);
  };

  const handleTooltipOpen = () => {
    setOpen(true);
  };

  useEffect(() => {
    dispatch(getByIdService(servicesId));
    dispatch(setIsDetailLoading(true));

    // eslint-disable-next-line
  }, []);

  const { Services, isLoadingServices } = useSelector(
    ({ Pay24Reducer }) => Pay24Reducer
  );

  useEffect(() => {
    if (Array.isArray(Services))
      setCountry(
        Services[0] && JSON.parse(Services[0]?.data)?.provider["@country"]
      );
    allCountries.push("–ö—ã—Ä–≥—ã–∑—Å—Ç–∞–Ω");

    // eslint-disable-next-line
  }, [Services]);

  const [filter, setFilter] = useState("");

  const filteredCoin = React.useMemo(
    () =>
      Services.filter((item) =>
        item.name
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );
  const ServicesList = filter ? filteredCoin : Services;

  const countries = Services.map(
    (item) => JSON.parse(item.data).provider["@country"]
  );

  let allCountries = countries?.filter(
    (item, pos) => countries.indexOf(item) == pos
  );

  return (
    <Box component="section" mt={main && 4} mb={main && 4}>
      <Container maxWidth="lg">
        {!main && <Balance />}

        <CabinetTop
          payment
          title={t("payments")}
          setFilter={setFilter}
          input
          placeHolder="–ü–æ–∏—Å–∫ —É—Å–ª—É–≥..."
        />

        <Box
          display="flex"
          flexDirection={{ xs: "column", lg: "row" }}
          alignItems="start"
        >
          <Box
            sx={{
              background: "#fff",
              boxShadow: " 0px 1px 0px #E2E8F0",
              borderRadius: "13px",
              display: "flex",
              flexDirection: "column",
              padding: "24px",
            }}
            width={{ xs: "100%", md: "30%" }}
            rowGap="16px"
            mr={{ xs: 0, lg: 2 }}
            mb={{ xs: 2, lg: 0 }}
            display="flex"
            flexDirection="column"
          >
            {allCountries
              ?.sort()
              .reverse()
              .map((item) => (
                <Button
                  variant="contained"
                  className={
                    country === item ? classes.button_active : classes.button
                  }
                  size="large"
                  onClick={() => setCountry(item)}
                >
                  {item}
                </Button>
              ))
              .reverse()}
          </Box>
          {/* )} */}
          <Grid container spacing={2}>
            {isLoadingServices
              ? Array.from(Array(20).keys()).map((item, idx) => (
                  <Grid item xs={12} sm={6} md={4} lg={4} xl={4}>
                    <SkeletonPayments />
                  </Grid>
                ))
              : Services &&
                ServicesList.filter(
                  (item) =>
                    JSON.parse(item.data).provider["@country"] === country &&
                    item.id !== 644
                ).map((item) => (
                  <Grid item xs={12} sm={6} md={4} lg={4} xl={4}>
                    {item.active === "1" ? (
                      <Box sx={{ position: "relative" }}>
                        <div className={`${classes.box} disabled`}>
                          <img src={item.url_logo} alt="" />
                          <Typography className={classes.title}>
                            {item.name}
                          </Typography>
                        </div>
                        <ClickAwayListener onClickAway={handleTooltipClose}>
                          <div>
                            <Tooltip
                              PopperProps={{
                                disablePortal: true,
                              }}
                              arrow
                              sx={{ maxWidth: "unset" }}
                              title={t("unavailable")}
                              placement="top-start"
                              onClose={handleTooltipClose}
                              open={open}
                              disableFocusListener
                              disableHoverListener
                              disableTouchListener
                            >
                              <IconButton
                                sx={{
                                  position: "absolute!important",
                                  bottom: 8,
                                  right: 8,
                                  color: "#777",
                                  fontSize: "12px",
                                  fontWeight: "600",
                                  display: "flex",
                                  alignItems: "center",
                                }}
                                onClick={handleTooltipOpen}
                              >
                                <ErrorOutlineIcon />
                              </IconButton>
                            </Tooltip>
                          </div>
                        </ClickAwayListener>
                      </Box>
                    ) : (
                      <Link
                        disabled
                        className={classes.box}
                        to={
                          !main
                            ? `/dashboard/payment-for-services/${servicesId}/${item.api_id}`
                            : `/main/payment-for-services/binance-pay/${servicesId}/${item.api_id}`
                        }
                      >
                        <img src={item.url_logo} alt="" />
                        <Typography className={classes.title}>
                          {item.name}
                        </Typography>
                      </Link>
                    )}
                  </Grid>
                ))}
          </Grid>
        </Box>
      </Container>
    </Box>
  );
};

export default Services;

const useStyles = makeStyles((theme) => ({
  box: {
    position: "relative",
    display: "flex",
    background: "#FFFFFF",

    height: "104px",
    padding: "8px 8px 8px 16px",
    justifyContent: "flex-start",
    alignItems: "center",
    overflow: "hidden",
    transition: "all 0.2s linear",
    boxShadow: "0px 1px 0px #E2E8F0",
    borderRadius: "8px",
    "&:hover": {
      boxShadow: "0px 0px 6px 2px #E2E8F0",
    },

    "&.disabled": {
      cursor: "not-allowed",
      opacity: 0.4,
      boxShadow: "0px 1px 0px #E2E8F0!important",
    },

    "& img": {
      width: "40px",
      height: "40px",
      objectFit: "contain",
      marginRight: 16,
    },
  },

  title: {
    fontWeight: "500",
    fontSize: "18px",
    lineHeight: "23px",
    letterSpacing: "-0.02em",
    color: "#000000",
  },

  button: {
    width: "100%",
    background: "transparent",
    color: "#B8C0CC",

    "&:hover": {
      "& .MuiButton-label": {
        color: "#000",
      },
    },
  },

  button_active: {
    width: "100%",
    background: "#FCE34D",
    color: "#000",
  },
}));
