import React, { useEffect, useState } from "react";
import {
  Container,
  Grid,
  makeStyles,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import Balance from "../../../components/Balance";
import CabinetTop from "../../../components/cabinetTop";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  getCategories,
  setLoading,
  setName,
} from "../../../redux/reducers/pay24";
import SkeletonPayments from "./Skeleton";
import { Box } from "@mui/material";

import binancePayLg2 from "../../../assets/images/binance-pay/binance-pay-2.webp";
import binancePayXs2 from "../../../assets/images/binance-pay/binance-pay-1.webp";
import binancePayLg from "../../../assets/images/dashboard/main/banners/binance-pay.webp";
import binancePayXs from "../../../assets/images/dashboard/main/banners/binance-pay-xs.webp";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.min.css";

const PaymentForServices = ({ t, main }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const theme = useTheme();
  const sm = useMediaQuery(theme.breakpoints.down(768));

  const [filter, setFilter] = useState("");

  const { isLoading } = useSelector(({ Pay24Reducer }) => Pay24Reducer);

  const filteredCoin = React.useMemo(
    () =>
      Categories.filter((item) =>
        item.name
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );
  const CategoriesList = filter ? filteredCoin : Categories;

  useEffect(() => {
    dispatch(setLoading(true));
    dispatch(getCategories());
    // eslint-disable-next-line
  }, []);

  return (
    <>
      {main && <Box sx={{ height: 10, background: "#000" }} />}

      {main && (
        <Swiper
          className={classes.slider}
          loop={true}
          slidesPerView={1}
          autoplay={{
            delay: 5000,
            pauseOnMouseEnter: true,
            disableOnInteraction: false,
          }}
        >
          <SwiperSlide className={classes.slide}>
            <img
              src={sm ? binancePayXs2 : binancePayLg2}
              className={classes.img}
              alt="img"
            />
          </SwiperSlide>
        </Swiper>
      )}

      <Box component="section" mt={main && 4} mb={main && 4}>
        <Container maxWidth="lg">
          {!main && <Balance />}

          <CabinetTop
            payment
            title={t("payments")}
            setFilter={setFilter}
            input
            placeHolder="–ü–æ–∏—Å–∫ —É—Å–ª—É–≥..."
          />

          <Grid container spacing={2}>
            {isLoading
              ? Array.from(Array(20).keys()).map((item, idx) => (
                  <Grid item xs={12} sm={6} md={4} lg={3} xl={3}>
                    <SkeletonPayments />
                  </Grid>
                ))
              : CategoriesList.map((item) => (
                  <Grid item xs={12} sm={6} md={4} lg={3} xl={3}>
                    <Link
                      className={classes.box}
                      onClick={() => dispatch(setName(item.name))}
                      to={
                        !main
                          ? `/dashboard/payment-for-services/${item.api_id}`
                          : `/main/payment-for-services/binance-pay/${item.api_id}`
                      }
                    >
                      <img src={item.logo_url} alt="" />
                      <Typography className={classes.title}>
                        {item.name}
                      </Typography>
                    </Link>
                  </Grid>
                ))}
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default PaymentForServices;

